.btn {
  box-sizing: border-box;
  font-size: 16px;
  line-height: 2.5;
  min-height: 2.5em;
  padding: 0 1.2em;
  outline: none;
  text-align: center;
  background: #6ec0e6;
  color: #ffffff;
  border: solid 1px #6ec0e6;
  border-radius: 4px;
  cursor: pointer;
  will-change: background;
  -webkit-transition: background ease-out 0.3s;
  transition: background ease-out 0.3s; }
  .btn:active, .btn:hover, .btn:focus {
    background: #84c9ea; }
  .btn_spinner {
    font-size: 0.8em;
    display: inline-block;
    margin-left: 1em; }
  .btn__outline {
    background: #ffffff;
    color: #6ec0e6; }
    .btn__outline:active, .btn__outline:hover, .btn__outline:focus {
      background: #f2f2f2; }
  .btn:disabled, .btn:disabled:hover, .btn:disabled:active, .btn:disabled:focus, .btn__disabled, .btn__disabled:hover, .btn__disabled:active, .btn__disabled:focus {
    background: grey;
    border-color: grey; }

.btnList {
  padding: 0;
  margin: 0;
  display: inline-flex; }
  .btnList .btn {
    display: inline;
    margin-right: 16px; }
    .btnList .btn:last-child {
      margin-right: 0; }
