.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  font-size: 30px;
  background-color: #213945;
  min-height: 2em;
  display: flex;
  flex-direction: column;
  align-items: left;
  color: white;
  -webkit-transition-delay: 5;
  transition-delay: 5;
  will-change: all;
  -webkit-transition: all ease-out 1s;
  transition: all ease-out 1s; }
  .header__small {
    background: red;
    font-size: 10px; }
